"use client";

import { useState, useEffect } from "react";
import { useRouter, useParams } from "next/navigation";
import {
  Plus,
  Trash2,
  Type,
  Heading1,
  Image as ImageIcon,
  List,
  Quote,
  X,
  Bold,
  Italic,
  Underline,
} from "lucide-react";

export default function EditPost() {
  const router = useRouter();
  const params = useParams();
  const postId = params.id;
  const [activeTab, setActiveTab] = useState("content");
  const [loadingPost, setLoadingPost] = useState(true);
  const [formData, setFormData] = useState({
    title: "",
    excerpt: "",
    category: "",
    featuredImage: "",
    status: "draft",
    metaTitle: "",
    metaDescription: "",
    metaKeywords: "",
    ogTitle: "",
    ogDescription: "",
    ogImage: "",
  });
  const [contentBlocks, setContentBlocks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [showMenuFor, setShowMenuFor] = useState(null);

  // Load post data on mount
  useEffect(() => {
    if (postId) {
      fetchPost();
    }
  }, [postId]);

  const fetchPost = async () => {
    try {
      setLoadingPost(true);
      setError("");
      const response = await fetch(`/api/admin/posts/${postId}`);
      
      if (!response.ok) {
        const errorData = await response.json().catch(() => ({ error: "Failed to load post" }));
        setError(errorData.error || "Failed to load post");
        setLoadingPost(false);
        return;
      }

      const data = await response.json();
      const post = data.post;

      if (post) {
        // Pre-fill form data
        setFormData({
          title: post.title || "",
          excerpt: post.excerpt || "",
          category: post.category || "",
          featuredImage: post.featuredImage || "",
          status: post.status || "draft",
          metaTitle: post.seo?.metaTitle || "",
          metaDescription: post.seo?.metaDescription || "",
          metaKeywords: post.seo?.metaKeywords || "",
          ogTitle: post.seo?.ogTitle || "",
          ogDescription: post.seo?.ogDescription || "",
          ogImage: post.seo?.ogImage || "",
        });

        // Pre-fill content blocks (ensure they have IDs)
        if (post.contentBlocks && Array.isArray(post.contentBlocks) && post.contentBlocks.length > 0) {
          const blocksWithIds = post.contentBlocks.map((block, index) => ({
            ...block,
            id: block.id || Date.now() + index + Math.random(),
          }));
          setContentBlocks(blocksWithIds);
        } else {
          // If no contentBlocks, create one from content
          if (post.content) {
            const textBlock = {
              id: Date.now(),
              type: "text",
              content: post.content,
              bold: false,
              italic: false,
              underline: false,
              color: "",
            };
            setContentBlocks([textBlock]);
          }
        }
      }
    } catch (err) {
      console.error("Error fetching post:", err);
      setError("Failed to load post. Please try again.");
    } finally {
      setLoadingPost(false);
    }
  };

  const blockTypes = [
    { type: "text", label: "Text", icon: Type },
    { type: "heading", label: "Heading", icon: Heading1 },
    { type: "image", label: "Image", icon: ImageIcon },
    { type: "list", label: "List", icon: List },
    { type: "quote", label: "Quote", icon: Quote },
  ];

  const addBlock = (index, type) => {
    const date = Date.now();
    const newBlock = {
      id: date + Math.random(),
      type,
      content: type === "list" ? [{ text: "", bold: false, italic: false, underline: false, color: "" }] : "",
      image: type === "image" ? "" : null,
      alt: type === "image" ? "" : null,
      level: type === "heading" ? 2 : null, // Default to h2 for headings
      heading: type === "list" ? "" : null, // List heading
      ordered: type === "list" ? false : null, // Numbered (true) or bulleted (false)
      bold: type === "text" ? false : null, // Text formatting
      italic: type === "text" ? false : null,
      underline: type === "text" ? false : null,
      color: type === "text" ? "" : null,
    };
    const newBlocks = [...contentBlocks];
    newBlocks.splice(index + 1, 0, newBlock);
    setContentBlocks(newBlocks);
    setShowMenuFor(null);
  };

  const updateBlock = (id, field, value) => {
    setContentBlocks(
      contentBlocks.map((block) =>
        block.id === id ? { ...block, [field]: value } : block
      )
    );
  };

  const deleteBlock = (id) => {
    setContentBlocks(contentBlocks.filter((block) => block.id !== id));
  };

  const addListItem = (blockId) => {
    setContentBlocks(
      contentBlocks.map((block) =>
        block.id === blockId
          ? { ...block, content: [...(block.content || []), { text: "", bold: false, italic: false, underline: false, color: "" }] }
          : block
      )
    );
  };

  const updateListItem = (blockId, itemIndex, field, value) => {
    setContentBlocks(
      contentBlocks.map((block) =>
        block.id === blockId
          ? {
              ...block,
              content: block.content.map((item, idx) =>
                idx === itemIndex 
                  ? typeof item === 'string' 
                    ? { text: value, bold: false, italic: false, underline: false, color: "" }
                    : { ...item, [field]: value }
                  : item
              ),
            }
          : block
      )
    );
  };

  const deleteListItem = (blockId, itemIndex) => {
    setContentBlocks(
      contentBlocks.map((block) =>
        block.id === blockId
          ? {
              ...block,
              content: block.content.filter((_, idx) => idx !== itemIndex),
            }
          : block
      )
    );
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleImageUpload = async (e, fieldName) => {
    const file = e.target.files[0];
    if (!file) return;

    // For now, we'll create a data URL. In production, upload to a service like Cloudinary
    const reader = new FileReader();
    reader.onloadend = () => {
      setFormData({
        ...formData,
        [fieldName]: reader.result,
      });
    };
    reader.readAsDataURL(file);
  };

  const handleBlockImageUpload = async (e, blockId) => {
    const file = e.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.onloadend = () => {
      updateBlock(blockId, "image", reader.result);
    };
    reader.readAsDataURL(file);
  };

  const handleSubmit = async (e, customData = null) => {
    e.preventDefault();
    setError("");
    setLoading(true);

    try {
      const dataToUse = customData || formData;

      // Validate that there's at least some content
      if (contentBlocks.length === 0) {
        setError("Please add at least one content block");
        setLoading(false);
        return;
      }

      // Check if all blocks are empty
      const hasContent = contentBlocks.some((block) => {
        if (
          block.type === "text" ||
          block.type === "heading" ||
          block.type === "quote"
        ) {
          return block.content && block.content.trim() !== "";
        }
        if (block.type === "list") {
          return (
            block.content &&
            block.content.some((item) => {
              const text = typeof item === 'string' ? item : item?.text;
              return text && text.trim() !== "";
            })
          );
        }
        if (block.type === "image") {
          return block.image && block.image.trim() !== "";
        }
        return false;
      });

      if (!hasContent) {
        setError("Please add content to at least one block");
        setLoading(false);
        return;
      }

      // Convert content blocks to content string
      const content =
        contentBlocks.length > 0
          ? contentBlocks
              .map((block) => {
                if (block.type === "text") return block.content || "";
                if (block.type === "heading") {
                  if (!block.content) return "";
                  const level = block.level || 2;
                  const hashes = "#".repeat(level);
                  return `${hashes} ${block.content}`;
                }
                if (block.type === "quote")
                  return block.content ? `> ${block.content}` : "";
                if (block.type === "list") {
                  const listItems = (block.content || []).filter(
                    (item) => {
                      const text = typeof item === 'string' ? item : item?.text;
                      return text && text.trim() !== "";
                    }
                  );
                  if (listItems.length === 0) return "";
                  const prefix = block.ordered ? "" : "- ";
                  return listItems.map((item, idx) => {
                    const text = typeof item === 'string' ? item : item?.text || "";
                    const num = block.ordered ? `${idx + 1}. ` : prefix;
                    return `${num}${text}`;
                  }).join("\n");
                }
                if (block.type === "image") {
                  return block.image
                    ? `![${block.alt || "Image"}](${block.image})`
                    : "";
                }
                return "";
              })
              .filter(Boolean)
              .join("\n\n")
          : "";

      const seoData = {
        metaTitle: dataToUse.metaTitle || dataToUse.title,
        metaDescription: dataToUse.metaDescription || dataToUse.excerpt,
        metaKeywords: dataToUse.metaKeywords,
        ogTitle: dataToUse.ogTitle || dataToUse.title,
        ogDescription: dataToUse.ogDescription || dataToUse.excerpt,
        ogImage: dataToUse.ogImage || dataToUse.featuredImage,
        twitterCard: "summary_large_image",
      };

      const postData = {
        title: dataToUse.title,
        excerpt: dataToUse.excerpt,
        content: content,
        category: dataToUse.category,
        featuredImage: dataToUse.featuredImage,
        status: dataToUse.status,
        contentBlocks: contentBlocks,
        seo: seoData,
      };

      const response = await fetch(`/api/admin/posts/${postId}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(postData),
      });

      const data = await response.json();
      console.log(data);

      if (!response.ok) {
        const errorMessage = data.error || data.message || "Failed to update post";
        const errorDetails = data.details || data.errorName || "";
        setError(`${errorMessage}${errorDetails ? ` (${errorDetails})` : ""}`);
        console.error("Post update error:", {
          status: response.status,
          error: data.error,
          errorName: data.errorName,
          details: data.details,
          fullData: data
        });
        setLoading(false);
        return;
      }

      // Success - redirect
      router.push("/admin/posts");
      router.refresh();
    } catch (err) {
      console.error("Post update exception:", err);
      setError(`An error occurred: ${err.message || "Please try again."}`);
      setLoading(false);
    }
  };

  const categories = [
    "technology",
    "business",
    "startups",
    "funding",
    "innovation",
    "entrepreneurship",
    "success-stories",
    "guides",
    "tools",
    "legal",
    "marketing",
  ];

  const renderBlock = (block, index) => {
    const BlockIcon =
      blockTypes.find((b) => b.type === block.type)?.icon || Type;

    return (
      <div key={block.id} className="flex items-start gap-2 mb-2 group">
        {/* Plus button on the left */}
        <div className="relative flex-shrink-0 pt-2">
          <button
            type="button"
            onClick={() =>
              setShowMenuFor(showMenuFor === block.id ? null : block.id)
            }
            className="w-6 h-6 flex items-center justify-center rounded-full border-2 border-dashed border-gray-300 dark:border-gray-600 hover:border-red-500 dark:hover:border-red-400 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors flex-shrink-0"
          >
            <Plus size={16} className="text-gray-400 dark:text-gray-500" />
          </button>

          {/* Block type menu */}
          {showMenuFor === block.id && (
            <div className="absolute left-8 top-0 z-50 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded shadow-lg p-1.5 min-w-[180px]">
              <div className="flex items-center justify-between mb-1.5 pb-1.5 border-b border-gray-200 dark:border-gray-700">
                <span className="text-xs font-semibold text-gray-700 dark:text-gray-300">
                  Add Block
                </span>
                <button
                  type="button"
                  onClick={() => setShowMenuFor(null)}
                  className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                >
                  <X size={14} />
                </button>
              </div>
              <div className="space-y-0.5">
                {blockTypes.map((blockType) => {
                  const Icon = blockType.icon;
                  return (
                    <button
                      key={blockType.type}
                      type="button"
                      onClick={() => addBlock(index, blockType.type)}
                      className="w-full flex items-center gap-1.5 px-2 py-1.5 hover:bg-gray-100 dark:hover:bg-gray-700 rounded text-xs text-left"
                    >
                      <Icon
                        size={16}
                        className="text-gray-600 dark:text-gray-400"
                      />
                      <span className="text-gray-700 dark:text-gray-300">
                        {blockType.label}
                      </span>
                    </button>
                  );
                })}
              </div>
            </div>
          )}
        </div>

        {/* Block content */}
        <div className="flex-1 flex items-start gap-3">
          <div className="flex-1">
            {block.type === "text" && (
              <div className="space-y-2">
                <textarea
                  value={block.content}
                  onChange={(e) =>
                    updateBlock(block.id, "content", e.target.value)
                  }
                  rows={3}
                  className="w-full px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white resize-none"
                  placeholder="Enter text content..."
                />
                <div className="flex items-center gap-2 flex-wrap">
                  <span className="text-xs font-medium text-gray-700 dark:text-gray-300">Format:</span>
                  <button
                    type="button"
                    onClick={() => updateBlock(block.id, "bold", !block.bold)}
                    className={`p-1.5 rounded border ${
                      block.bold
                        ? "bg-blue-500 text-white border-blue-500"
                        : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                    }`}
                    title="Bold"
                  >
                    <Bold size={14} />
                  </button>
                  <button
                    type="button"
                    onClick={() => updateBlock(block.id, "italic", !block.italic)}
                    className={`p-1.5 rounded border ${
                      block.italic
                        ? "bg-blue-500 text-white border-blue-500"
                        : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                    }`}
                    title="Italic"
                  >
                    <Italic size={14} />
                  </button>
                  <button
                    type="button"
                    onClick={() => updateBlock(block.id, "underline", !block.underline)}
                    className={`p-1.5 rounded border ${
                      block.underline
                        ? "bg-blue-500 text-white border-blue-500"
                        : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                    }`}
                    title="Underline"
                  >
                    <Underline size={14} />
                  </button>
                  <div className="flex items-center gap-1">
                    <label className="text-xs text-gray-600 dark:text-gray-400">Color:</label>
                    <input
                      type="color"
                      value={block.color || "#000000"}
                      onChange={(e) => updateBlock(block.id, "color", e.target.value)}
                      className="w-8 h-6 border border-gray-300 dark:border-gray-600 rounded cursor-pointer"
                      title="Text Color"
                    />
                  </div>
                </div>
              </div>
            )}

            {block.type === "heading" && (
              <div className="space-y-2">
                <div className="flex items-center gap-2">
                  <label className="text-xs font-medium text-gray-700 dark:text-gray-300 whitespace-nowrap">
                    Heading Level:
                  </label>
                  <select
                    value={block.level || 2}
                    onChange={(e) =>
                      updateBlock(block.id, "level", parseInt(e.target.value))
                    }
                    className="px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white"
                  >
                    <option value={1}>H1 - Largest</option>
                    <option value={2}>H2 - Large</option>
                    <option value={3}>H3 - Medium</option>
                    <option value={4}>H4 - Small</option>
                    <option value={5}>H5 - Smaller</option>
                    <option value={6}>H6 - Smallest</option>
                  </select>
                </div>
                <input
                  type="text"
                  value={block.content}
                  onChange={(e) =>
                    updateBlock(block.id, "content", e.target.value)
                  }
                  className={`w-full px-3 py-1.5 font-bold border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white ${
                    block.level === 1
                      ? "text-3xl"
                      : block.level === 2
                      ? "text-2xl"
                      : block.level === 3
                      ? "text-xl"
                      : block.level === 4
                      ? "text-lg"
                      : block.level === 5
                      ? "text-base"
                      : "text-sm"
                  }`}
                  placeholder="Enter heading..."
                />
              </div>
            )}

            {block.type === "image" && (
              <div className="space-y-2">
                <input
                  type="url"
                  value={block.image || ""}
                  onChange={(e) =>
                    updateBlock(block.id, "image", e.target.value)
                  }
                  className="w-full px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white"
                  placeholder="Image URL"
                />
                <div className="flex items-center gap-2">
                  <span className="text-sm text-gray-500 dark:text-gray-400">OR</span>
                  <label className="flex-1">
                    <input
                      type="file"
                      accept="image/*"
                      onChange={(e) => handleBlockImageUpload(e, block.id)}
                      className="hidden"
                    />
                    <span className="inline-block px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer text-sm text-center">
                      Upload Image
                    </span>
                  </label>
                </div>
                <input
                  type="text"
                  value={block.alt || ""}
                  onChange={(e) => updateBlock(block.id, "alt", e.target.value)}
                  className="w-full px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white"
                  placeholder="Image alt text (optional)"
                />
                {block.image && (
                  <img
                    src={block.image}
                    alt={block.alt || "Preview"}
                    className="w-full max-w-md h-48 object-cover rounded-lg border border-gray-200 dark:border-gray-700"
                    onError={(e) => {
                      e.target.style.display = "none";
                    }}
                  />
                )}
              </div>
            )}

            {block.type === "list" && (
              <div className="space-y-3 border border-gray-200 dark:border-gray-700 rounded-lg p-3 bg-gray-50 dark:bg-gray-900/50">
                {/* List Heading */}
                <div className="space-y-1">
                  <label className="text-xs font-medium text-gray-700 dark:text-gray-300">
                    List Heading (Optional):
                  </label>
                  <input
                    type="text"
                    value={block.heading || ""}
                    onChange={(e) => updateBlock(block.id, "heading", e.target.value)}
                    className="w-full px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white"
                    placeholder="Enter list heading..."
                  />
                </div>

                {/* List Style Selector */}
                <div className="flex items-center gap-2">
                  <label className="text-xs font-medium text-gray-700 dark:text-gray-300 whitespace-nowrap">
                    List Style:
                  </label>
                  <select
                    value={block.ordered ? "ordered" : "unordered"}
                    onChange={(e) => updateBlock(block.id, "ordered", e.target.value === "ordered")}
                    className="px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white"
                  >
                    <option value="unordered">Bulleted (•)</option>
                    <option value="ordered">Numbered (1, 2, 3...)</option>
                  </select>
                </div>

                {/* List Items */}
                <div className="space-y-2">
                  {(block.content || []).map((item, itemIndex) => {
                    const itemText = typeof item === 'string' ? item : item?.text || "";
                    const itemBold = typeof item === 'string' ? false : item?.bold || false;
                    const itemItalic = typeof item === 'string' ? false : item?.italic || false;
                    const itemUnderline = typeof item === 'string' ? false : item?.underline || false;
                    const itemColor = typeof item === 'string' ? "" : item?.color || "";
                    
                    return (
                      <div key={itemIndex} className="flex items-start gap-2 p-2 border border-gray-200 dark:border-gray-700 rounded bg-white dark:bg-gray-800">
                        <span className="text-gray-500 dark:text-gray-400 mt-1.5 text-sm">
                          {block.ordered ? `${itemIndex + 1}.` : "•"}
                        </span>
                        <div className="flex-1 space-y-2">
                          <input
                            type="text"
                            value={itemText}
                            onChange={(e) => updateListItem(block.id, itemIndex, "text", e.target.value)}
                            className="w-full px-2 py-1 border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 dark:bg-gray-700 dark:text-white text-xs"
                            placeholder="List item text"
                          />
                          <div className="flex items-center gap-2 flex-wrap">
                            <button
                              type="button"
                              onClick={() => updateListItem(block.id, itemIndex, "bold", !itemBold)}
                              className={`p-1.5 rounded border ${
                                itemBold
                                  ? "bg-blue-500 text-white border-blue-500"
                                  : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                              }`}
                              title="Bold"
                            >
                              <Bold size={14} />
                            </button>
                            <button
                              type="button"
                              onClick={() => updateListItem(block.id, itemIndex, "italic", !itemItalic)}
                              className={`p-1.5 rounded border ${
                                itemItalic
                                  ? "bg-blue-500 text-white border-blue-500"
                                  : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                              }`}
                              title="Italic"
                            >
                              <Italic size={14} />
                            </button>
                            <button
                              type="button"
                              onClick={() => updateListItem(block.id, itemIndex, "underline", !itemUnderline)}
                              className={`p-1.5 rounded border ${
                                itemUnderline
                                  ? "bg-blue-500 text-white border-blue-500"
                                  : "bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 border-gray-300 dark:border-gray-600 hover:bg-gray-200 dark:hover:bg-gray-600"
                              }`}
                              title="Underline"
                            >
                              <Underline size={14} />
                            </button>
                            <div className="flex items-center gap-1">
                              <label className="text-xs text-gray-600 dark:text-gray-400">Color:</label>
                              <input
                                type="color"
                                value={itemColor || "#000000"}
                                onChange={(e) => updateListItem(block.id, itemIndex, "color", e.target.value)}
                                className="w-8 h-6 border border-gray-300 dark:border-gray-600 rounded cursor-pointer"
                                title="Text Color"
                              />
                            </div>
                            <button
                              type="button"
                              onClick={() => deleteListItem(block.id, itemIndex)}
                              className="ml-auto p-1.5 text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 rounded"
                              title="Delete item"
                            >
                              <Trash2 size={14} />
                            </button>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                  <button
                    type="button"
                    onClick={() => addListItem(block.id)}
                    className="mt-1 px-2 py-1 text-xs border border-dashed border-gray-300 dark:border-gray-600 rounded hover:bg-gray-100 dark:hover:bg-gray-800 text-gray-600 dark:text-gray-400"
                  >
                    + Add List Item
                  </button>
                </div>
              </div>
            )}

            {block.type === "quote" && (
              <div className="border-l-4 border-red-500 pl-4">
                <textarea
                  value={block.content}
                  onChange={(e) =>
                    updateBlock(block.id, "content", e.target.value)
                  }
                  rows={3}
                  className="w-full px-3 py-1.5 text-sm border border-gray-300 dark:border-gray-600 rounded focus:ring-1 focus:ring-red-500 focus:border-red-500 italic dark:bg-gray-700 dark:text-white resize-none"
                  placeholder="Enter quote text..."
                />
              </div>
            )}
          </div>

          {/* Delete button */}
          <button
            type="button"
            onClick={() => deleteBlock(block.id)}
            className="flex-shrink-0 p-1.5 text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 rounded opacity-0 group-hover:opacity-100 transition-opacity"
          >
            <Trash2 size={14} />
          </button>
        </div>
      </div>
    );
  };

  return (
    <div className="max-w-5xl mx-auto space-y-4">
      <div>
        <h1 className="text-xl font-bold text-gray-900 dark:text-white">
          Edit Post
        </h1>
        <p className="text-xs text-gray-500 dark:text-gray-400 mt-0.5">
          Update your blog post with SEO optimization
        </p>
      </div>

      {error && (
        <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 text-red-700 dark:text-red-400 px-3 py-2 rounded text-xs">
          {error}
        </div>
      )}

      {loadingPost ? (
        <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 p-8 text-center">
          <p className="text-gray-600 dark:text-gray-400">Loading post data...</p>
        </div>
      ) : (
      <div className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
        <div className="flex border-b border-gray-200 dark:border-gray-700">
          <button
            type="button"
            onClick={() => setActiveTab("content")}
            className={`flex-1 px-4 py-2 text-xs font-medium transition-colors ${
              activeTab === "content"
                ? "text-red-600 dark:text-red-400 border-b-2 border-red-600 dark:border-red-400 bg-red-50 dark:bg-red-900/20"
                : "text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white"
            }`}
          >
            Content
          </button>
          <button
            type="button"
            onClick={() => {
              setActiveTab("seo");
              setShowMenuFor(null);
            }}
            className={`flex-1 px-4 py-2 text-xs font-medium transition-colors ${
              activeTab === "seo"
                ? "text-red-600 dark:text-red-400 border-b-2 border-red-600 dark:border-red-400 bg-red-50 dark:bg-red-900/20"
                : "text-gray-600 dark:text-gray-400 hover:text-gray-900 dark:hover:text-white"
            }`}
          >
            SEO Settings
          </button>
        </div>

        <form onSubmit={handleSubmit} className="p-4 space-y-4">
          {activeTab === "content" && (
            <div className="space-y-4">
              <div>
                <label className="block text-xs font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Post Title <span className="text-red-500">*</span>
                </label>
                <input
                  type="text"
                  name="title"
                  value={formData.title}
                  onChange={handleChange}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                  placeholder="Enter post title"
                  required
                />
              </div>

              <div>
                <label className="block text-xs font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Excerpt / Short Description
                </label>
                <textarea
                  name="excerpt"
                  value={formData.excerpt}
                  onChange={handleChange}
                  rows={2}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500 resize-none"
                  placeholder="Brief description of your post"
                />
              </div>

              <div>
                <label className="block text-xs font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Category <span className="text-red-500">*</span>
                </label>
                <select
                  name="category"
                  value={formData.category}
                  onChange={handleChange}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                  required
                >
                  <option value="">Select a category</option>
                  {categories.map((cat) => (
                    <option key={cat} value={cat}>
                      {cat.charAt(0).toUpperCase() +
                        cat.slice(1).replace("-", " ")}
                    </option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-xs font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Featured Image
                </label>
                <div className="space-y-2">
                  <input
                    type="url"
                    name="featuredImage"
                    value={formData.featuredImage}
                    onChange={handleChange}
                    className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                    placeholder="https://example.com/image.jpg"
                  />
                  <div className="flex items-center gap-2">
                    <span className="text-xs text-gray-500 dark:text-gray-400">OR</span>
                    <label className="flex-1">
                      <input
                        type="file"
                        accept="image/*"
                        onChange={(e) => handleImageUpload(e, "featuredImage")}
                        className="hidden"
                      />
                      <span className="inline-block w-full px-3 py-1.5 border border-gray-300 dark:border-gray-600 rounded hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer text-xs text-center">
                        Upload Image
                      </span>
                    </label>
                  </div>
                  {formData.featuredImage && (
                    <img
                      src={formData.featuredImage}
                      alt="Preview"
                      className="w-full max-w-md h-32 object-cover rounded border border-gray-200 dark:border-gray-700"
                      onError={(e) => {
                        e.target.style.display = "none";
                      }}
                    />
                  )}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Content <span className="text-red-500">*</span>
                </label>

                <div className="border border-gray-200 dark:border-gray-700 rounded p-3 bg-gray-50 dark:bg-gray-900/50 min-h-[150px]">
                  {contentBlocks.length === 0 ? (
                    <div className="text-center py-6">
                      <p className="text-xs text-gray-500 dark:text-gray-400 mb-3">
                        Click the + button below to add your first content block
                      </p>
                      <button
                        type="button"
                        onClick={() => {
                          const newBlock = {
                            id: Date.now(),
                            type: "text",
                            content: "",
                          };
                          setContentBlocks([newBlock]);
                        }}
                        className="inline-flex items-center gap-1.5 px-3 py-1.5 text-xs bg-red-500 text-white rounded hover:bg-red-600 transition"
                      >
                        <Plus size={18} />
                        Add First Block
                      </button>
                    </div>
                  ) : (
                    <div className="space-y-0">
                      {contentBlocks.map((block, index) =>
                        renderBlock(block, index)
                      )}

                      {/* Plus button at the end */}
                      <div className="flex items-start gap-3 mt-3">
                        <button
                          type="button"
                          onClick={() =>
                            setShowMenuFor(showMenuFor === "end" ? null : "end")
                          }
                          className="w-8 h-8 flex items-center justify-center rounded-full border-2 border-dashed border-gray-300 dark:border-gray-600 hover:border-red-500 dark:hover:border-red-400 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors flex-shrink-0"
                        >
                          <Plus
                            size={16}
                            className="text-gray-400 dark:text-gray-500"
                          />
                        </button>

                        {showMenuFor === "end" && (
                          <div className="relative z-50 bg-white dark:bg-gray-800 border border-gray-200 dark:border-gray-700 rounded shadow-lg p-1.5 min-w-[180px]">
                            <div className="flex items-center justify-between mb-1.5 pb-1.5 border-b border-gray-200 dark:border-gray-700">
                              <span className="text-xs font-semibold text-gray-700 dark:text-gray-300">
                                Add Block
                              </span>
                              <button
                                type="button"
                                onClick={() => setShowMenuFor(null)}
                                className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                              >
                                <X size={14} />
                              </button>
                            </div>
                            <div className="space-y-1">
                              {blockTypes.map((blockType) => {
                                const Icon = blockType.icon;
                                return (
                                  <button
                                    key={blockType.type}
                                    type="button"
                                    onClick={() =>
                                      addBlock(
                                        contentBlocks.length - 1,
                                        blockType.type
                                      )
                                    }
                                    className="w-full flex items-center gap-2 px-3 py-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded text-sm text-left"
                                  >
                                    <Icon
                                      size={16}
                                      className="text-gray-600 dark:text-gray-400"
                                    />
                                    <span className="text-gray-700 dark:text-gray-300">
                                      {blockType.label}
                                    </span>
                                  </button>
                                );
                              })}
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Status
                </label>
                <select
                  name="status"
                  value={formData.status}
                  onChange={handleChange}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                >
                  <option value="draft">Draft</option>
                  <option value="published">Published</option>
                </select>
              </div>
            </div>
          )}

          {activeTab === "seo" && (
            <div className="space-y-4">
              <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded p-3">
                <p className="text-xs text-red-800 dark:text-red-200">
                  <strong>Tip:</strong> SEO fields are optional. If left empty,
                  they will automatically use your post title and excerpt.
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Meta Title
                </label>
                <input
                  type="text"
                  name="metaTitle"
                  value={formData.metaTitle}
                  onChange={handleChange}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                  placeholder="SEO title for search engines"
                  maxLength={60}
                />
                <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
                  {formData.metaTitle.length}/60 characters
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Meta Description
                </label>
                <textarea
                  name="metaDescription"
                  value={formData.metaDescription}
                  onChange={handleChange}
                  rows={3}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                  placeholder="SEO description for search engines"
                  maxLength={160}
                />
                <p className="mt-1 text-xs text-gray-500 dark:text-gray-400">
                  {formData.metaDescription.length}/160 characters
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                  Meta Keywords
                </label>
                <input
                  type="text"
                  name="metaKeywords"
                  value={formData.metaKeywords}
                  onChange={handleChange}
                  className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                  placeholder="Comma-separated keywords"
                />
              </div>

              <div className="border-t border-gray-200 dark:border-gray-700 pt-6">
                <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                  Open Graph (Social Media)
                </h3>

                <div className="space-y-3">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      OG Title
                    </label>
                    <input
                      type="text"
                      name="ogTitle"
                      value={formData.ogTitle}
                      onChange={handleChange}
                      className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                      placeholder="Title for social media sharing"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      OG Description
                    </label>
                    <textarea
                      name="ogDescription"
                      value={formData.ogDescription}
                      onChange={handleChange}
                      rows={3}
                      className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                      placeholder="Description for social media sharing"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                      OG Image
                    </label>
                    <div className="space-y-3">
                      <input
                        type="url"
                        name="ogImage"
                        value={formData.ogImage}
                        onChange={handleChange}
                        className="w-full rounded border border-gray-300 dark:border-gray-600 dark:bg-gray-700 dark:text-white px-3 py-1.5 text-sm focus:outline-none focus:ring-1 focus:ring-red-500"
                        placeholder="Image URL for social media"
                      />
                      <div className="flex items-center gap-2">
                        <span className="text-sm text-gray-500 dark:text-gray-400">
                          OR
                        </span>
                        <label className="flex-1">
                          <input
                            type="file"
                            accept="image/*"
                            onChange={(e) => handleImageUpload(e, "ogImage")}
                            className="hidden"
                          />
                          <span className="inline-block px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 cursor-pointer text-sm">
                            Upload Image
                          </span>
                        </label>
                      </div>
                      {formData.ogImage && (
                        <img
                          src={formData.ogImage}
                          alt="OG Preview"
                          className="w-full h-48 object-cover rounded-lg border border-gray-200 dark:border-gray-700"
                          onError={(e) => {
                            e.target.style.display = "none";
                          }}
                        />
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}

          <div className="flex items-center justify-between pt-6 border-t border-gray-200 dark:border-gray-700">
            <button
              type="button"
              onClick={() => router.back()}
              className="px-6 py-3 rounded-xl border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700 transition"
            >
              Cancel
            </button>
            <div className="flex gap-3">
              <button
                type="button"
                onClick={(e) => {
                  e.preventDefault();
                  const draftData = { ...formData, status: "draft" };
                  handleSubmit(e, draftData);
                }}
                disabled={loading}
                className="px-6 py-3 rounded-xl border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 hover:bg-gray-50 dark:hover:bg-gray-700 transition disabled:opacity-50"
              >
                Save Draft
              </button>
              <button
                type="submit"
                disabled={loading}
                className="bg-gradient-to-r from-red-500 to-red-600 text-white px-6 py-3 rounded-xl hover:from-red-600 hover:to-red-700 shadow-lg shadow-red-500/25 transition-all duration-200 font-medium disabled:opacity-50 disabled:cursor-not-allowed"
              >
                {loading ? "Updating..." : "Update Post"}
              </button>
            </div>
          </div>
        </form>
      </div>
      )}
    </div>
  );
}
